import { Controller, Node, Commands } from '@ali/4ever-cangjie';

export default function removePureLink(
  controller: Controller,
  node: Node,
  offset: number,
  href: string,
) {
  const { selection } = controller.value;
  const range = selection
    .moveToStartOfNode(node, controller)
    .moveForward(offset)
    .moveFocusForward(href.length);

  return controller.command('deleteAtRange', range).command(Commands.focus);
}
